// here goes interface buttons  and links 
//buttons and access

*:focus {outline:none}
a{
	border-color:black;
	padding: 5px;
	text-decoration: none;
	color:black;
}
a:hover{
	color:white;
}

#links{
	a{
		border-color:white;
		padding: 5px;
		text-decoration: none;
		color:black;
	}
	a:hover{
		border-bottom-style: solid;
		border-width: thin;
	
	}
}



button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}
button {
    display: inline-block;
    border: none;
    padding: 1rem 2rem;
    margin: 0;
    text-decoration: none;
    background:black;
    color: #ffffff;
    font-size: 1rem;
    cursor: pointer;
    text-align: center;
    transition: background 250ms ease-in-out, 
                transform 150ms ease;
    -webkit-appearance: none;
    -moz-appearance: none;
}

button:hover,
button:focus {
	background: black;
	opacity: 0.7;
}

button:focus {
	background: white;
	color: black;
	transition: background 250ms ease-in-out, 
                transform 150ms ease;
    -webkit-appearance: none;
    -moz-appearance: none;
}

button:active {
    transform: scale(0.99);
}

//scroll bar  settings on ly works on chrome 

/* width */
::-webkit-scrollbar {
	width: 5px;
	scrollbar-width: 5px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
	background: transparent; 
	opacity: 1;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
	background: rgb(0, 0, 0); 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
	background: rgba($color: #030303, $alpha: 1.0);
  }
